// Tests finaux pour atteindre 100% de couverture
it('should clear previous timeout on rapid updates', async () => {
    // 1. Rendu du composant
    render(
        <ProgressProvider>
            <TestComponent />
        </ProgressProvider>
    );

    // 2. Attendre le chargement initial
    await act(async () => {
        jest.runAllTimers();
    });

    // 3. Déclencher une mise à jour qui va démarrer le timeout de 500ms
    act(() => {
        fireEvent.press(screen.getByText('Update Streak'));
    });

    // 4. Avancer les timers de moins de 500ms (par ex. 250ms)
    // Cela empêche la première sauvegarde de se déclencher
    jest.advanceTimersByTime(250);

    // 5. Déclencher une deuxième mise à jour
    // C'est cette action qui va évaluer le "if (saveTimeoutRef.current)" à true et exécuter clearTimeout()
    act(() => {
        fireEvent.press(screen.getByText('Update Vocab Level 1'));
    });

    // 6. Avancer le temps pour déclencher le dernier save
    // Seul le second timeout devrait se déclencher, car le premier a été annulé
    await act(async () => {
        jest.runAllTimers();
    });

    // 7. Vérifier que AsyncStorage.setItem a été appelé une seule fois
    // Cela prouve que le premier timeout a été correctement annulé
    expect(AsyncStorage.setItem).toHaveBeenCalledTimes(1);
});

//Tests branches

it('should definitively clear the previous timeout on rapid updates', async () => {
    // 1. Activer les timers factices.
    jest.useFakeTimers();
    const clearTimeoutSpy = jest.spyOn(global, 'clearTimeout');

    // 2. Rendre le composant.
    render(
        <ProgressProvider>
            <TestComponent />
        </ProgressProvider>
    );

    // 3. Passer le chargement initial.
    await act(async () => {
        jest.runAllTimers();
    });

    // 4. Déclencher une première mise à jour d'état qui va programmer un timeout.
    await act(async () => {
        fireEvent.press(screen.getByText('Update Streak'));
    });

    // ÉTAPE CRUCIALE : Attendre une microtâche pour garantir que le ref est mis à jour.
    await Promise.resolve();

    // 5. Avancer le temps de 1 ms pour s'assurer que le timeout est bien programmé.
    jest.advanceTimersByTime(1);

    // 6. Déclencher une deuxième mise à jour.
    // Cela force la réexécution de l'effet, qui va appeler clearTimeout.
    await act(async () => {
        fireEvent.press(screen.getByText('Update Vocab Level 1'));
    });

    // 7. Vérifier que clearTimeout a été appelé.
    expect(clearTimeoutSpy).toHaveBeenCalledWith(expect.any(Object));

    // 8. Exécuter tous les timers restants et vérifier la sauvegarde.
    await act(async () => {
        jest.runAllTimers();
    });
    expect(AsyncStorage.setItem).toHaveBeenCalledTimes(1);

    // 9. Nettoyer le mock.
    clearTimeoutSpy.mockRestore();
    jest.useRealTimers();
});

it('should calculate level progress correctly when there are no exercises for a level', async () => {
  const customInitialState = {
    // Il faut que la structure corresponde à `progress.levels`
    levels: {
      'level-1': { exercises: [] }
    }
  };

  AsyncStorage.getItem.mockResolvedValue(JSON.stringify(customInitialState));
  
  render(
    <ProgressProvider>
      <TestComponent />
    </ProgressProvider>
  );
  
  await act(async () => {
    jest.runAllTimers();
  });
  
  const { getLevelProgress } = await waitFor(() => screen.getByTestId('test-component').props);
  expect(getLevelProgress('level-1')).toBe(0);
});

it('should correctly calculate global progress without a bonus level', async () => {
    const customStateWithoutBonus = {
        'level-1': { exercises: [{ progress: 50 }] },
        'level-2': { exercises: [{ progress: 100 }] },
    };

    // 1. On configure le mock AsyncStorage.
    AsyncStorage.getItem.mockResolvedValue(JSON.stringify(customStateWithoutBonus));

    // 2. On rend le composant sans `act`.
    render(
        <ProgressProvider>
            <TestComponent />
        </ProgressProvider>
    );

    // 3. On utilise `act` pour les opérations asynchrones.
    await act(async () => {
        jest.runAllTimers();
    });

    const { getGlobalProgress } = await waitFor(() => screen.getByTestId('test-component').props);
    expect(getGlobalProgress()).toBe(75);
});

